import create from 'zustand';
import {persist} from "zustand/middleware"
import { ethers, providers } from 'ethers';
import WalletConnectProvider from "@walletconnect/web3-provider";
import Web3 from 'web3'
import { getAssetPrice } from '../utils/utils'
// and modify our existing state
const { REACT_APP_NETWORK_ID } = process.env;

const getWeb3 = () => {
    return new Promise(async (resolve, reject) => {
      if (window.ethereum) {
        const web3 = new Web3(window.ethereum)
        try {
          // await window.ethereum.send("eth_requestAccounts");
          await window.ethereum.enable()
          resolve(web3)
        } catch (e) {
          reject(e)
        }
      } else if (window.web3) {
        resolve(window.web3)
      } else {
        window.alert('Must install Metamask Extension!\nDApp will not load')
        // resolve('Must install Metamask Extension!')
      }
    })
 

}

let store = (set) => ({
    priceLoading:false,
    web3:undefined,
    account: "",
    isTrustWallet: false,
    asset : "",
    assetPrice:0,
    walletType: "",
    modal : false,
    wrongNetwork:false,
     disconnectWallet: () => {
      //  localStorage.setItem('wallet', '');
        set((state)=>({
            walletType:"",
            account: ""
        }))
    
      },
     
     connectMetamask: async() => {

        const web3 = await getWeb3()
        if (web3 === undefined) return
        const account = (await web3.eth.getAccounts())[0]
        const networkId = await web3.eth.net.getId()
        if (networkId !== parseInt(REACT_APP_NETWORK_ID)) {
          console.log('Not correct', networkId, REACT_APP_NETWORK_ID)
         // setWrongNetwork(true)
         set((state)=>({...state, wrongNetwork:true}))

        }
        const domain = window.location.href.split('?')[0]
        if (domain.indexOf('/busd') === -1) {
         // setAsset('BNB')
         set((state)=>({...state, asset:"BNB"}))
        } else {
         // setAsset('BUSD')
          set((state)=>({...state, asset:"BUSD"}))
        }
        const bnbPrice = await getAssetPrice(web3)
       // setAssetPrice(bnbPrice)
       set((state)=>({...state, assetPrice:bnbPrice,web3:web3,account:account}))
       // setWeb3(web3)
       // setAccount(account)
        // if(window.ethereum) {
        //   try {
        //     await window.ethereum
        //     .request({ method: 'eth_requestAccounts' })
        //     .then(accounts => {
        //       set((state)=>({
        //         ...state,
        //         account:accounts[0],
        //         isTrustWallet:false,
        //         walletType:'metamask'
        //     }))
        //     });
        //   } catch (error) {
        //     console.error(error)
        //    // ErrorHandling(error)
        //   }
        // } else {
        //   console.error('Metamask not Detected!')
        // }
      },
      connectTrustWallet: async() => {
        try {
            //  Create WalletConnect Provider
            const provider = new WalletConnectProvider({
              rpc: {
                56: "https://bsc-dataseed.binance.org"
              },
              qrcodeModalOptions: {
                mobileLinks: ["trust"]
              }
            });
      
            //  Enable session (triggers QR Code modal)
            await provider.enable();
      
            const web3Provider = new providers.Web3Provider(provider);
      
            const accounts = await web3Provider.listAccounts();
            set((state)=>({
                ...state,
                account:accounts[0],
                isTrustWallet:true,
                walletType:'walletconnect'
            }))
         } catch (error) {
            console.log(error)
          }
      },
     
  setModal: async (_modal) => {
    set((state) => ({
      ...state,
      modal: _modal
    }));
  },
});
// persist the created state
store = persist(store, {name: "wallet"})
// create the store
const useStore = create(store);

export default  useStore;