function NavbarLink({icon: Icon, label,path:to, active, onClick }) {
  const { classes, cx } = useStyles();
  return (
    <Tooltip label={label} position="right" withArrow transitionDuration={0}>
      <UnstyledButton
       onClick={onClick}
       component={Link} to={to}
      //  component={Link} to="/dashboard/my-investments"
        className={cx(classes.link, { [classes.active]: active })}>
        <Icon />
      </UnstyledButton>
    </Tooltip>
  );
}

